'use client'
import { useEffect, useState } from 'react';
import { useParams } from 'next/navigation'; 
import { getDetails, getRecommendations, getCredits, getProviders, getReviews, getActorMovies } from '@/lib/tmdb';
import DetailsClient from '@/components/DetailsClient';

export default function DetailsPage() {
  const { type, id } = useParams();  // Acceder a los params en la ruta dinámica

  const [data, setData] = useState(null);
  const [recommendations, setRecommendations] = useState([]);
  const [castData, setCastData] = useState([]);
  const [providers, setProviders] = useState([]);
  const [reviews, setReviews] = useState([]);

  useEffect(() => {
    const fetchDetails = async () => {
      if (!type || !id) {
        console.error("Missing type or id.");
        return;
      }

      let recommendationsData = [];
      let reviewsData = [];
      let providersData = [];
      let castDataResponse = [];

      const details = await getDetails(type, id);
      setData(details);

      if (type !== 'person') {
        recommendationsData = await getRecommendations(type, id);
        reviewsData = await getReviews(type, id);
        providersData = await getProviders(type, id);
      }

      // Si es un actor, obtenemos la filmografía
      if (type === 'person') {
        const actorMovies = await getActorMovies(id); // Películas en las que el actor ha trabajado
        castDataResponse = actorMovies?.cast || [];
      } else {
        castDataResponse = await getCredits(type, id);  // Para películas/series obtenemos los créditos
      }

      setRecommendations(recommendationsData?.results || []);
      setReviews(reviewsData?.results || []);
      setProviders(providersData?.results?.ES?.flatrate || []);
      setCastData(castDataResponse?.cast || []);
    };

    fetchDetails();
  }, [type, id]);

  if (!data) return;

  // Si no es un actor, usamos el componente de detalles de películas/series
  return (
    <DetailsClient 
      type={type} 
      id={id} 
      data={data} 
      recommendations={recommendations} 
      castData={castData} 
      providers={providers} 
      reviews={reviews} 
    />
  );
}
